
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Util from '../libs/util'
export default {
components: {
Timeline,
TimelineItem,
XButton,
Cell,
CellBox,
CellFormPreview,
Group,
Flexbox,
FlexboxItem
},
data () {
return {
count: 3,
title2: '商品：32件',
list: [{
label: '漓泉白啤酒330ml*6瓶',
value: '10件'
}, {
label: 'Banana',
value: '1.04'
}, {
label: 'Fish',
value: '8.00'
}],
showContent002: false,
title3: '赠品：漓泉白啤酒330ml*6瓶',
value3: '3瓶',
}
},
methods: {
onClick () {
console.log('on click')
}
},
created: function () {
var status = this.$route.query.status;
var payStatus = this.$route.query.paystatus;

console.log(status, payStatus);
console.log(typeof status);
switch(status){
case '0':
Util.title('全部订单');
break;
case '1':
if(payStatus=='0'){
Util.title('未发货|未付款');
}else if(payStatus==1){
Util.title('未发货|已付款');
}
break;
case '2':
if(payStatus=='0'){
Util.title('未收货|未付款');
}else if(payStatus==1){
Util.title('未收货|已付款');
}
break;
case '3':
if(payStatus=='0'){
Util.title('已收货|未付款');
}
break;
case '4':
Util.title('已完成');
break;
case '5':
Util.title('已取消');
break;

}
}
}



